<template>
  <div>
     <table class="table">
  <thead class="thead-dark">
    <tr>
      <th scope="col">id</th>
      <th scope="col">Nom</th>
      <th scope="col">Prénom</th>
      <th scope="col">Pause demande à :</th>
      <th scope="col">Validé à :/ par : </th>
      <th scope="col" width="200">En attente de Validation</th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="pause in listePause" :key="pause.Id_break">
      <th scope="row">{{pause.Id_break}}</th>
      <td>{{pause.First_Name}}</td>
      <td>{{pause.First_Name}}</td>
      <td>{{pause.Date_Time_Req}}</td>
      <td>{{pause.Date_Time_Val}}</td>
      <td>
          <div v-if="pause.Time_val_Ag!=null">
        <v-btn color="primary" disabled>Pris</v-btn>
        </div>
        <div v-else>
          <div v-if="pause.Date_Time_Val==null">
            <v-btn color="success" disabled>en attente de validation</v-btn>
        </div>
        <div v-else>
          <div v-if="pause.Id_Agent==id_User">
            <v-btn color="success" @click="validationPauseAgent(pause.Id_break)">approuver</v-btn>
          </div>
          <div v-else>
            <v-btn color="success" disabled>approuver</v-btn>
          </div>
        </div>
        </div>
        </td>
    </tr>
  </tbody>
</table>
    </div>
</template>

<script>
import 'bootstrap/dist/css/bootstrap.min.css'
import axios from 'axios'
export default {
    props: {
  },
    data:()=>({
        listePause:null,
        selectable: false,
        auto: false,
        options: [
            { id: 2, name: 'Validation Manuel' },
            { id: 3, name: 'Validation Automatique'}
        ],
        agentsBefor:0,
        id_User:null
    }),
    mounted () {
              this.id_User=this.$store.getters.idUser
              this.getAllPauseNonValider();
              setInterval(() => this.getAllPauseNonValider(),30000);
        },
      created(){
    },
    methods:{
    getAllpause(){
      axios
                  .get(this.$store.getters.urlRequest+'requetteGsPause.php?getlistpause=tout')
                  .then(response => (this.listePause = response.data))
                  .catch(err => console.log(err))
                  
    },
    getAllPauseNonValider(){
      axios
                  .get(this.$store.getters.urlRequest+'requetteGsPause.php?getlistpause=nonValider&id_user='+this.id_User)
                  .then(response => (this.listePause = response.data.TableauDATA,
                                    this.agentsBefor=response.data.aBM))
                  .catch(err => console.log(err))
                  
    },
    validationPauseAgent(idBreakapprouveAgent){
      axios
                  .get(this.$store.getters.urlRequest+'setRequest.php?breakValidationAgent='+idBreakapprouveAgent)
                  .then(response => console.log(response.data))
                  .catch(err => console.log(err))
                  this.getAllPauseNonValider();
    }

    },
    watch:{
        agentsBefor:function(){
            this.$emit("agentsBeforChild",this.agentsBefor)
        },
    }
}
</script>