<template>
    <div class="text-center">
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on }">
        <v-btn
          class="mr-0"
            color="primary"
            d-flex align-end flex-column
            @click="breakReaquest"
            v-on="on"
        >
          demander une pause
        </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="headline grey lighten-2"
          primary-title
        >
          Statut
        </v-card-title>

        <v-card-text>
          {{responseServeur}}
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false"
          >
            OK
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    props:{
    },
    data:()=>({
        dialog: false,
        responseServeur:""
    }),
    methods:{
        breakReaquest(){
        axios
                  .get(this.$store.getters.urlRequest+'setRequest.php?break_requestforAgent='+this.$store.getters.idUser)
                  .then(response => (this.responseServeur=response.data))
                  .catch(err => (this.responseServeur=err))
      }
    }
}
</script>

