<template>
    <div class="text-center">
    <v-btn
      color="primary" @click="validationVigie(idBreak)">
      Valider
    </v-btn>

    <v-snackbar
      v-model="snackbar"
      :timeout="timeout"
    >
      {{ repServer }}
      <v-btn
        color="blue"
        text
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    props:{
        idBreak:Number
    },
    data:()=>({
        snackbar: false,
        reponseServer:"",
      timeout: 2000,
      repServer:""
    }),
    methods:{
         validationVigie(idBreakValider){
        axios
                  .get(this.$store.getters.urlRequest+'setRequest.php?breakValidationVigie='+idBreakValider+'&sender='+this.$store.getters.idUser)
                  .then(response => (this.repServer = response.data))
                  .catch(err => this.repServer(err))
                  this.snackbar = true
    }
    }
}
</script>

