<template>
<div>
  <v-card width="400" class="mx-auto mt-5">
      <v-card-title class="pb-0">
        <h1>Login</h1>
      </v-card-title>
      <v-card-text>
        <v-form v-model="valid">
          <v-text-field 
            label="Username"
            :rules="usernameRules"
            v-model="User.username"
            prepend-icon="mdi-account-circle"
          />
          <v-text-field 
            :type="showPassword ? 'text' : 'password'" 
            label="Password"
            v-model="User.password"
            :rules="passwordRules"
            prepend-icon="mdi-lock"
            :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
            @click:append="showPassword = !showPassword"
            required
          />
          <v-select
          :items="projets"
          item-value="Id_Projet"
          v-model="User.select"
          :rules="[v => !!v || 'select your project, is required']"
          item-text="Label"
          label="Séléctionner votre projet"
          single-line
        ></v-select>
        </v-form>
      </v-card-text>
      <v-divider></v-divider>
      <v-card-actions>
        <v-btn 
        :disabled="!valid"
        color="success"
        class="mr-4"
        @click="validate">Login</v-btn>
      </v-card-actions>
    </v-card>
</div>
</template>

<script>
import axios from 'axios'
import store from '../store/index.js'
export default {
    data:()=> ({
      store:store,
        projets:[],
        User:{username: '', password: '', select:''},
        showPassword: false,
        valid: false,
        usernameRules: [
        v => !!v || 'Name is required',
        v => v.length >= 5 || 'Name must be more than 5 characters',
      ],
        passwordRules: [
        v => !!v || 'password is required',
        v => v.length >= 5 || 'Name must be more than 5 characters',
      ],
      sessionLog:[],
      dialog: false,
    }),
    mounted(){
      this.getAllprojects();

    },
    methods:{
      getAllprojects(){
        axios
                  .get(this.$store.getters.urlRequest+'requetteGsPause.php?getlistproject=tout')
                  .then(response => (this.projets = response.data))
                  .catch(err => console.log(err))
      },
      validate () {
        //this.$refs.form.validate()
        let newUser = 
            {username:this.User.username,
            password:this.User.password,
            select:this.User.select}
        axios
                  .post(this.$store.getters.urlRequest+'/auth.php',
                  'username='+newUser.username+
                  '&password='+newUser.password+
                  '&projet='+newUser.select,
                  { headers: {
                   'Content-type': 'application/x-www-form-urlencoded'

                  }})
                  .then(response => (this.sessionLog = response.data))
                  .catch(err => console.log(err))
                  if (this.sessionLog.auth=='authorized') {
                      switch(this.sessionLog.profile){
                            case 'agentviewer':
                                this.$store.commit('edit_dataapp', this.sessionLog.profile)
                                this.$store.commit('edit_UserId',this.sessionLog.id_agent)
                                this.$router.replace({ name: "Agent24" });
                                break;
                            case 'vigieviewer':
                                this.$store.commit('edit_dataapp', this.sessionLog.profile)
                                this.$store.commit('edit_UserId',this.sessionLog.id_agent)
                                this.$router.replace({ name: "Vigie53" });
                                break;
                            case 'responssableviewer':
                                this.$store.commit('edit_dataapp', this.sessionLog.profile)
                                this.$store.commit('edit_UserId',this.sessionLog.id_agent)
                                this.$router.replace({ name: "Responssable256" });
                                break;
                      }
                  }
                  else {
                  console.log("The username and / or password is incorrect");
        }


      },
    }
}
</script>

